import { Button, Card, Col, message, Row, Space, Typography } from 'antd'
import React from 'react'
import { howitworks, landing, offerings, testimonials } from './data'
import './Home.sass'
import LeadCollectionForm from './LeadCollectionForm'

const { Meta } = Card
const { Title } = Typography

class Home extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			visible: false
		}
	}

	onCreate = values => {
		//console.log('Received values of form: ', values)
		message.success('Thank you for showing interest. We will reach out to you shorty.')
		this.setState({
			visible: false
		})
	}

	render() {
		const { visible } = this.state
		return (
			<React.Fragment>
				<Row
					align="middle"
					className="container-row"
					justify="center"
					key="1"
				>
					<Space
						direction="vertical"
						size="large"
					>
						<Title>{landing.title}</Title>
						<Title level={4}>{landing.sub_title}</Title>
						<Button
							onClick={() => {
								this.setState({
									visible: true
								})
							}}
							shape="round"
							size="large"
							type="primary"
						>
							{landing.btn_title.toUpperCase()}
						</Button>
					</Space>
				</Row>
				<Row
					align="middle"
					className="container-row"
					justify="center"
					key="2"
				>
					<Space
						direction="vertical"
						size="large"
					>
						<Title level={2}>{howitworks.title}</Title>
						<Title level={4}>{howitworks.sub_title}</Title>
						<Row
							gutter={[48, 48]}
							justify="center"
						>
							{
								howitworks.content.map((item, index) => (
									<Col
										key={`howitwork-${index}`}
										lg={8}
										md={12}
										xs={20}
									>
										<Card
											bodyStyle={{ textAlign: "center" }}
											bordered={false}
										>
											<Meta
												title={item.title}
												description={item.sub_title}
											/>
										</Card>
									</Col>
								))
							}
						</Row>
					</Space>
				</Row>
				<Row
					align="middle"
					className="container-row"
					justify="center"
					key="3"
				>
					<Space
						direction="vertical"
						size="large"
					>
						<Title level={2}>{offerings.title}</Title>
						<Title level={4}>{offerings.sub_title}</Title>
						<Row
							gutter={[48, 48]}
							justify="center"
						>
							{
								offerings.content.map((offering, index) => (
									<Col
										key={`offering-${index}`}
										lg={8}
										md={12}
										xs={20}
									>
										<Card
											bodyStyle={{ textAlign: "center" }}
											bordered={false}
										>
											<Meta
												title={offering.title}
												description={offering.sub_title}
											/>
										</Card>
									</Col>
								))
							}
						</Row>
					</Space>
				</Row>
				<Row
					align="middle"
					className="container-row"
					justify="center"
					key="4"
				>
					<Space
						direction="vertical"
						size="large"
					>
						<Title level={2}>{testimonials.title}</Title>
						<Title level={4}>{testimonials.sub_title}</Title>
						<Row
							gutter={[48, 48]}
							justify="center"
						>
							{
								testimonials.content.map((testimonial, index) => (
									<Col 
										key={`testimonial-${index}`}
										lg={6}
										md={12}
										xs={20}  
									>
										<Card
											bodyStyle={{ textAlign: "center" }}
											bordered={false}
										>
											<Meta
												title={testimonial.title}
												description={testimonial.sub_title}
											/>
										</Card>
									</Col>
								))
							}
						</Row>
					</Space>
				</Row>
				<LeadCollectionForm
					visible={visible}
					onCreate={this.onCreate}
					onCancel={() => {
						this.setState({
							visible: false
						})
					}}
				/>
			</React.Fragment>
		)
	}
}
export default Home